@use "base/variables" as v;
@use "base/mixins" as m;

header {
  background: #fff;
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 50px;

  &__logo {
    font-size: 30px;
    text-transform: uppercase;
  }

  nav {
    ul {
      display: flex;

      li {
        a {
          display: inline-block;
          color: #000;
          padding: 5px 0;
          margin: 0 10px;
          border: 3px solid transparent;
          text-transform: uppercase;
          transition: 0.2s;

          &:hover,
          &.active {
            border-bottom-color: dodgerblue;
          }
        }
      }
    }
  }

  .hamburger {
    cursor: pointer;
    display: none;

    div {
      width: 30px;
      height: 3px;
      margin: 5px 0;
      background: #000;
    }
  }

  @media only screen and (max-width: 900px) {
    padding: 0 30px;
  }

  @media only screen and (max-width: 700px) {
    .hamburger {
      display: block;
    }

    nav {
      position: absolute;
      width: 100%;
      left: -100%;
      top: 70px;
      background: #fff;
      padding: 30px;
      transition: 0.3s;

      ul {
        display: block;

        li {
          a {
            margin: 5px 0;
          }
        }
      }
    }

    #nav_check:checked ~ nav {
      left: 0;
    }
  }
}
